.grid{
    width: 80%;
    height: 800px;
    margin: 0 auto;
    background-color: white;
    position: relative;
    font-size: 0;

    .wrapper{
        width: 95%;
        height: 95%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        .col{
            width: 25%;
            height: 100%;
            display: inline-block;

            .itemXL:nth-of-type(1){
                height: 66%;
                background-image: url(../imgs/img_dior_vestido01.jpg);
                background-repeat: no-repeat; 
                background-position: center;
                background-size: cover;
                position: relative;

                p{
                    width: 100%;
                    text-align: center;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    background-color: red;
                }
                
            }
            
            .item_dark:nth-of-type(1){
                height: 33%;
                background-image: url(../imgs/img_dior_oculos.jpeg);
                background-repeat: no-repeat; 
                background-position: center;
                background-size: auto;
            }

            .itemXL:nth-of-type(2){
                height: 66%;
                background-image: url(../imgs/img_dior_vestido02.jpg);
                background-repeat: no-repeat; 
                background-position: center;
                background-size: cover;
            }

            .item_dark:nth-of-type(2){
                height: 33%;
                background-image: url(../imgs/img_dior_bolsa01.jpeg);
                background-repeat: no-repeat; 
                background-position: center;
                background-size: auto;
            }

            .item_dark_2:nth-of-type(1){
                height: 33%;
                background-color: #9f9460;
            }

            .item{
                height: 33%;
            }

            

            
        }
    }
}